:root{
  --primary-color: #242423;
  --secondary-color: #E8EDDF;
  --background-primary-color: white;
  --background-secondary-color: #333533;
  --accent-color: #F5CB5C;
  --accent-secondary-color: #FF2E00;
}
/* LAYOUT */
body{
  background: var(--background-primary-color);
  font-family: 'Open Sans', sans-serif;
  transition: background 0.4s linear;

}

header{
  margin-bottom: 2em;
}
.footer{
  margin-top: 2em;
}
.col-md-12{
  width: auto;
  margin: auto;
  flex-basis: 40%;
}
.col-xs-4{
  width: auto;
  height: auto;
  margin: auto;
  padding: 0;
  flex-basis: 100%;
}
.col-xs-6{
  width: auto;
  margin: auto;
  padding: 0;
  flex-basis: 100%;
}
.col-xs-4 > img{
  width: 120%;
  height: 120%;
}
.container{
  margin-bottom: 1em;
}


/* ELEMENTS */
.textbox{
  font-size: 2vw;
  text-align: center;
}
.topbox {

  margin-top: 8em;
  cursor: pointer;
  text-align: center;
}
#logobox{
  max-width: 100%;
  max-height: 100%;
  cursor: pointer;
}
h1{
  font-size: 50px;
  cursor: pointer;
}

img {
  border: 1px solid black;
}
.nav-link-menu{
  padding:0;
  margin: 2em 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.nav-link-menu li{
  margin: 0;
  padding: 0 1em;
  list-style: none;
  font-size: 3vh;
}
.nav-link-menu a{
  color: var(--primary-color);
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
}
.social-links{
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.social-links li{
  list-style: none;
  text-align: center;
  font-size: 3vh;
}
.social-links a{
  color: var(--primary-color);
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
}

h5,h4,h2{
  padding: 0;
  margin: 0 ;
  font-size: 30px;
  text-align: center;
}

p {
  color: var(--primary-color);
  text-align: center;
}

/* EFFECTS */
.active.hidden,
.hidden{
  display: none;
}

.active{
  display: block;
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
}
.nav-link-menu li:hover{
  transform: scale(1.5);
  transition: transform .2s ease-in-out;
}

.nav-link-menu li:not( :hover ){ 
  transform: scale(1);
  transition: transform .2s ease-in-out; 
}
li > .active-link{
  color: var(--accent-color);
  box-shadow: 20px;
}

/* DARK MODE */
.dark {
  background: var(--primary-color);
}
body.dark *{
  color: var(--background-primary-color);
}
h1{
  text-align:center;
}

/*Queries*/

/*Animations*/
@-webkit-keyframes fadeIn {
  from { opacity: 0; }
    to { opacity: 1; }
}
@keyframes fadeIn {
  from { opacity: 0; }
    to { opacity: 1; }
}

